import React, { Component } from 'react';
import { compose } from 'recompose';
import x from "./../../image/x.png"

import { withFirebase } from '../Firebase';
import { withAuthorization } from '../Session';
import * as ROLES from '../../constants/roles';

class AdminPage_Users extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      users: [],
    };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.props.firebase.users().on('value', snapshot => {
      const usersObject = snapshot.val();

      const usersList = Object.keys(usersObject).map(key => ({
        ...usersObject[key],
        uid: key,
      }));
      this.setState({
        users: usersList,
        loading: false,

      });
      // console.log(usersList)
    });

  }

  componentWillUnmount() {
    this.props.firebase.users().off();
  }

  render() {
    const { users, loading } = this.state;

    return (
      <div>
        <h1 className="text-center">Manage Users:</h1>

        {loading && <div>Loading ...</div>}
        <ul className="mb-0">
          <li className="row">
            <div className="col-4 col_user p-0"><strong>ID:</strong></div>
            <div className="col-3 col_user"> <strong>Email</strong></div>
            <div className="col-2 col_user"><strong>Name</strong></div>
            <div className="col-2 col_user"><strong>Username</strong></div>
          </li>
        </ul>
        <UserList users={users} />
      </div>
    );
  }
}

const UserList = ({ users }) => (
  <ul>
    {users.map(user => (
      <li key={user.uid} className="row">



        <div className="col-4 col_user">
          {user.uid}
        </div>
        <div className="col-3 col_user">
          {user.email}
        </div>
        <div className="col-2 col_user">
          {user.name}
        </div>
        <div className="col-2 col_user">
          {user.username}
        </div>
        <button type="button" className="btn-small btn btn-outline-dark btn-sm  btn_delete "><img className="img_delete" src={x}></img></button>
      </li>
    ))}
  </ul>
);

const condition = authUser =>
  authUser && !!authUser.roles;

export default compose(
  withAuthorization(condition),
  withFirebase,
)(AdminPage_Users);